import React, { Component } from "react";
import styled from "styled-components";

export default class Channel extends Component {
  getTheme = () => {
    const { status } = this.props;
    switch (status) {
      case "live":
        return {
          fontcolor: "white",
          fontbgcolor: "red",
          fontcontent: "LIVE",
          bordercolor: "red",
        };
      case "wait":
        return {
          fontcolor: "white",
          fontbgcolor: "#34afeb",
          fontcontent: "WAIT",
          bordercolor: "#6a97ad",
        };
      case "off":
        return {
          fontcolor: "#f9fae6",
          fontbgcolor: "#616161",
          fontcontent: "OFF",
          bordercolor: "#616161",
        };
      default:
        return {
          fontcolor: "#f0d8e2",
          fontbgcolor: "#cb4ede",
          fontcontent: "FAIL",
          bordercolor: "#6c3075",
        };
    }
  };
  render() {
    const theme = this.getTheme();

    const ChannelItem = styled.div`
      margin: 0px 10px 20px 10px;
      padding: 10px;
      flex-basis: 15rem;
      border: solid 2px ${theme.bordercolor};
      border-radius: 5px;
    `;

    const Avatar = styled.img`
      position: relative;
      top: -1.1rem;
      left: -1.2rem;
      width: 5rem;
      height: 5rem;
      border-radius: 50%;
      border: 3px solid ${theme.bordercolor};
      background: ${theme.fontcolor};
    `;

    const Status = styled.p`
      position: relative;
      top: -2rem;
      left: 0rem;
      width: 46px;
      height: 20px;
      text-align: center;
      font-weight: bold;
      font-size: 16px;
      color: ${theme.fontcolor};
      background: ${theme.fontbgcolor};
    `;
    return (
      <ChannelItem>
        <div className="avatarbox">
          <Avatar src={this.props.avatar}></Avatar>

          <Status>{theme.fontcontent}</Status>
        </div>
        <a href={this.props.streamurl}>
          <div className="previewbox">
            <img src={this.props.thumbnail} alt="thumbnail" />
          </div>
        </a>
        <div className="channel_Description">
          <div className="channel_Title">
            <p>{this.props.title}</p>
          </div>
          <div className="channel_Owner">{this.props.owner}</div>
          <div className="channel_Views">{this.props.viewcount}</div>
          <div className="channel_Time">{this.props.starttime}</div>
        </div>
      </ChannelItem>
    );
  }
}

