import React, { Component } from "react";
import styled from "styled-components";
import "./index.css";

export default class Search extends Component {

  handleKeyUp = (e) => {
    console.log(e)
    const { keyCode, target } = e;

    // 測試你輸入的按鍵的keycode值
    // alert(keyCode)

    // 保存數據
    console.log(target.value);
    this.inputvalue = target.value;

    // 當按下enter時，提交輸入結果
    if (keyCode !== 13) {
      return; //不是enter就返回
    }

    // 提交數據
    this.props.onSubmit(this.inputvalue);
  };

  handleClick = (e) => {
    console.log(e)
    // 提交數據
    this.props.onSubmit(this.inputvalue);
  };

  handleTagClick(keyword){
    return ()=>this.props.onSubmit(keyword);
  }

  render() {
    return (
      <div className="searchbox">
        <button className="tagsButton" onClick={this.handleTagClick("live")}>LIVE</button>
        <button className="tagsButton" onClick={this.handleTagClick("wait")}>WAIT</button>
        <button className="tagsButton" onClick={this.handleTagClick("off")}>OFF</button>
        <button className="tagsButton" onClick={this.handleTagClick("all")}>ALL</button>
        <input className="searchInput"
          type="text"
          placeholder="Enter keywords to get some channels"
          onKeyUp={this.handleKeyUp}
        />
        <button className="searchButton" onClick={this.handleClick}>搜尋</button>
      </div>
    );
  }
}
