import React, { Component } from "react";
import { HashRouter, Link, Route } from "react-router-dom";
import Browse from "./pages/Browse";
import Channels from "./pages/Channels";
import Theater from "./pages/Theater/";
import Members from "./pages/Members";

export default class App extends Component {
  render() {
    return (
      <div>
        <HashRouter>
          <nav>
            <div className ="navbox">
              <Link className="navitem" to="/">L.K.DD</Link>
            </div>
            <div  className ="navbox">
              {/* <Link className="navitem" to="/browse/">瀏覽</Link> */}
              <Link className="navitem" to="/channels/">頻道</Link>
              <Link className="navitem" to="/theater/">影院</Link>
            </div>
            <div  className ="navbox">
              <Link className="navitem" to="/members/">會員</Link>
            </div>
          </nav>
          <Route exact path="/" component={Channels} />
          <Route path="/browse/" component={Browse} />
          <Route path="/channels/" component={Channels} />
          <Route path="/theater/" component={Theater} />
          <Route path="/members/" component={Members} />
        </HashRouter>
      </div>
    );
  }
}
