import axios from "axios";
import React, { Component } from "react";
import Search from "../../components/Search";
import ChannelList from "../../components/ChannelList";

export default class Channels extends Component {
  state = {
    channels: [],
  };

  componentDidMount() {
    console.log("props:", this.props);
    // 透過當前路徑去解析，取得要獲取的資源標籤
    this.getChannels("live");
  }

  getChannels(keyword) {
    this.setState({ channels: [] });
    axios.get(`http://lowkeydd.ddns.net/channels/${keyword}`).then(
    // axios.get(`http://127.0.0.1:3000/channels/${keyword}`).then(
      (response) => {
        console.log("成功了, 回應如下:\n", response.data);
        const { channels } = response.data;
        this.setState({ channels });
      },
      (error) => {
        console.log("失敗了, 錯誤如下:\n", error);
      }
    );
  }

  onSubmit = (value) => {
    this.getChannels(value);
  };

  render() {
    return (
      <div>
        <Search onSubmit={this.onSubmit} />
        <ChannelList items={this.state.channels} />
      </div>
    );
  }
}
